<template>
  <div class="board-wrapper">
    <div class="board">
      <BoardItem :game-status="gameStatus" v-for="field in fields"  :field="field" :key="'item-' + field.id"
      @selectField="selectField($event)"/>
    </div>

    <p class="difficult">Сложность: <strong>{{ difficult }}</strong></p>
    <p class="win" v-if="isWin">Ура! Продолжаем =)</p>
    <p class="fail" v-if="isFail">Мда... Проиграл =(</p>

    <button class="btn" @click="start" :disabled="!canStartGame">Играть</button>
  </div>
</template>

<script>
import BoardItem from "@/components/BoardItem";
import useGameInit from "@/components/composables/useGameInit";
import useGameStart from "@/components/composables/useGameStart";
import useGameProcess from "@/components/composables/useGameProcess";
import {GAME_STATUS} from "@/constants";
import { ref } from 'vue'

export default {
  name: "PlayBoard",
  props: {},
  components: {
    BoardItem,
  },
  setup() {
    const number = 25;
    const gameStatus = ref(GAME_STATUS.NONE)

    const { difficult, fields, init } = useGameInit(number);

    const { start, canStartGame } = useGameStart(init, fields, difficult, number, gameStatus)

    const { selectField, isWin, isFail } = useGameProcess(fields, gameStatus, difficult, start)

    return {
      number,
      difficult,
      fields,
      init,
      start,
      gameStatus,
      canStartGame,
      selectField,
      isWin,
      isFail,

    }
  },
}
</script>

<style scoped>

.board {
  width: 300px;
  padding: 2px 3px 0px 3px;
  background: #494846;
  margin: 0 auto;
  border-radius: 5px;
}
.btn {
  background: #6d6d6b;
  color: white;
  border: none;
  border-radius: 2px;
  padding: 10px 30px;
  margin: 10px;
  cursor: pointer;
  outline: none;
  transition: .4s;
}

.btn:hover {
  background: #9A7165EF;
}
.btn:disabled {
  opacity: 50%;
}
.win {
  color: #42b983;
}
.fail {
  color: #FF0000FF;
}
</style>